// THIS FILE KNOWS HOW TO MAKE ALL THE ACTION
// OBJECDTS THAT WE WILL USE. ACTIONS ARE SIMPLE
// LITTLE PACKAGES THAT REPRESENT SOME EVENT
// THAT WILL BE DISPATCHED TO THE STORE, WHICH
// WILL TRIGGER THE EXECUTION OF A CORRESPONDING
// REDUCER, WHICH ADVANCES STATE

// THESE ARE ALL THE TYPE OF ACTIONS WE'LL BE CREATING
export const REGISTER_SUCCESS = 'REGISTER_SUCCESS';
export const REGISTER_ERROR = 'REGISTER_ERROR';
export const LOGIN_SUCCESS = 'LOGIN_SUCCESS';
export const LOGIN_ERROR = 'LOGIN_ERROR';
export const LOGOUT_SUCCESS = 'LOGOUT_SUCCESS';
export const UPDATE_WIREFRAMER = 'UPDATE_WIREFRAMER';
export const DELETE_WIREFRAMER_ERROR = 'DELETE_WIREFRAMER_ERROR';
export const SAVE_WIREFRAMER_ERROR = "SAVE_WIREFRAMER_ERROR";

// THESE CREATORS MAKE ACTIONS ASSOCIATED WITH USER ACCOUNTS

export function registerSuccess() {
    return { type: 'REGISTER_SUCCESS' }
};
export function registerError(error) { 
    return { type: 'REGISTER_ERROR', error }
};
export function loginSuccess() {
    return { type: 'LOGIN_SUCCESS' }
};
export function loginError(error) {
    return { type: 'LOGIN_ERROR', error }
};
export function logoutSuccess() {
    return { type: 'LOGOUT_SUCCESS' }
};

export function updateWireframer(wireframer){
    return {
        type: 'UPDATE_WIREFRAMER',
        wireframer
    }
}
export function updateInitialWireframer(wireframer){
    return {
        type: 'INITIAL_WIREFRAMER',
        wireframer
    }
}

export function updateSelectedControl(control){
    return{
        type: 'UPDATE_SELECTED_CONTROL',
        control
    }
}

// THESE CREATORS MAKE ACTIONS FOR ASYNCHRONOUS TODO LIST UPDATES
export function createTodoList(todoList) {
    return {
        type: 'CREATE_TODO_LIST',
        todoList
    }
}
export function createTodoListError(error) {
    return {
        type: 'CREATE_TODO_LIST_ERROR',
        error
    }
}
export function createWireframer(wireframer){
    return {
        type: "CREATE_WIREFRAMER",
        wireframer
    }
}
export function createWireframerError(error) {
    return {
        type: 'CREATE_WIREFRAMER_ERROR',
        error
    }
}
export function fatherTime(time){
    return{
        type: "MODIFY_LAST_VIEWED",
        time
    }
}
export function fatherTimeError(time){
    return{
        type: "MODIFY_LAST_VIEWED_ERROR",
        time
    }
}
